.model {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  vertical-align: middle;
}

.gurlContainer {
  position: relative;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.gurl {
  height: 80vh;
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}

.spinner {
  display: block;
  position: absolute;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  cursor: pointer;
}

.arm {
  top: 48%;
  left: 75%;
}

.leg {
  top: 93%;
  left: 54%;
}

.hair {
  top: 4%;
  left: 60%;
}

