.gMap {
  position: relative;
}
  .hover-question {
    position: absolute;
    margin: 0;
    padding: 0;
    -webkit-box-shadow: 0px 0px 23px -1px rgba(0, 0, 0, 0.45);
    -moz-box-shadow: 0px 0px 23px -1px rgba(0, 0, 0, 0.45);
    box-shadow: 0px 0px 23px -1px rgba(0, 0, 0, 0.45);
    display: block;
    z-index: 100;

  }
  .hover-text,  .onhover-quiz-bttn, .onhover-findoutMore-bttn {
    position: absolute;
  }
  
