body {
    height: 100%;
    width: 100%;
    margin:0;
    padding:0;
}

.signup-form {
    position: absolute!important;
    left: 4%;
  
}

.modal-content {
    border: 1px solid white!important;
}

.input:hover {
    border: 1px solid rgb(177, 118, 177);
}

.mySignUp-input {
    width: 100%;
    font-size: 12px;
    margin: 5px;
    padding: 10px;
    border-radius: 25px;
    border: 1px solid grey;
    height: 30px;
}

.mySignUp-bttn{ 
    text-transform: uppercase;
    padding: 5px;
    border-radius: 10px; 
    cursor: pointer; 
    background-color: transparent;
    color: darkgray;
    font-size: 12px;
    border-color:darkgray;
    height: 35px;
    width: 130px;
}

.button:hover {
    background-color: lightgray;
    color: white;
    font-weight: 700;
    border-color:darkgray;
}

.errorMessage {
    color: #ce1313;
    padding-top: 3px;
    padding-bottom: 3px;
    font-size: 14px;
}

.message {
    color: darkgrey;
    padding-top: 2px;
    padding-bottom: 2px;
    font-size: 10px;
    width: 100%;
}

.inputError {
    border: #ce1313;
}

@media (max-width: 560px) {
    .signup-form {
        top:  190px;
        left:4%;  
        width: 320px;     
        position: absolute!important;

    }
}

@media (max-width: 768px) {
  
.signup-form {
  top:166px;
  left:440px;
  width: 260px;
  position: absolute!important;

}

}

